<template>
    
    <div>
        <button type="button" :class="props.buttonClass" @click="dialogVisible = true" v-html="props.buttonText"></button>

        <Dialog v-model:visible="dialogVisible" modal header="Yeni Aile Ekle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="create">

                <div class="d-flex flex-row align-items-end">
                    <div class="symbol symbol-100px">
                        <img :src="form.mainImage.preview_url" v-if="form.mainImage"/>
                        <div class="symbol-label" v-else>
                            <i class="bi bi-person fs-2x"></i>
                        </div>
                    </div>
                    <div class="ms-3 d-flex flex-row align-items-center">
                        <PopupMediaLibrary :on-select="setMainImage" :button-text="'Seç'" :mime-type="'image/'" :key="Math.floor(Math.random() * 100000)"/>
                        <button type="button" class="btn btn-sm btn-light-danger ms-2" v-if="form.mainImage" @click="form.mainImage = null">Kaldır</button>
                    </div>
                </div>

                <Divider/>

                <div class="rounded p-5 bg-light border mb-5">
                    <h5 class="card-title text-muted">Genel Bilgiler</h5>
                    <Divider/>
                    <div class="row g-4 g-xl-6">
                        <div class="col-12">
                            <div class="d-flex align-items-center mb-1">
                                <span class="fw-semibold">Alt Kategori</span>
                                <div class="d-flex align-items-center ms-2">
                                    <img :src="`/dmn/media/flags/${form.language}.svg`" class="me-2" style="width: 14px" />
                                    <span class="text-uppercase">{{ form.language }}</span>
                                </div>
                            </div>
                            <Dropdown filter v-model="form.photo_sub_category_id" :options="props.photoCategories" optionGroupLabel="name" optionLabel="name" optionValue="id" optionGroupChildren="photo_sub_categories" class="w-100" />
                        </div>
                        <div class="col-lg-6">
                            <div class="d-flex flex-column">
                                <div class="d-flex align-items-center mb-1">
                                    <span class="fw-semibold text-uppercase">İsim</span>
                                    <div class="d-flex align-items-center ms-2">
                                        <img :src="`/dmn/media/flags/${form.language}.svg`" class="me-2" style="width: 14px" />
                                        <span class="text-uppercase">{{ form.language }}</span>
                                    </div>
                                </div>
                                <InputText v-model="form.name" class="w-100" :class="{'p-invalid':form.errors.name}"/>
                            </div>
                        </div>
                        <div class="col-lg-6">
                            <div class="d-flex flex-column">
                                <div class="d-flex align-items-center mb-1">
                                    <span class="fw-semibold text-uppercase">Bilinen Ad</span>
                                    <div class="d-flex align-items-center ms-2">
                                        <img :src="`/dmn/media/flags/${form.language}.svg`" style="width: 14px" />
                                    </div>
                                </div>
                                <AutoComplete v-model="form.name_known" :suggestions="autoCompleteItems" @complete="search" inputClass="w-100" :class="{'p-invalid':form.errors.name_known}"/>
                            </div>
                        </div>
                        <div class="col-12">
                            <div class="d-flex flex-column">
                                <div class="d-flex align-items-center mb-1">
                                    <span class="fw-semibold text-uppercase">Bilinen Diğer Adlar</span>
                                    <div class="d-flex align-items-center ms-2">
                                        <img :src="`/dmn/media/flags/${form.language}.svg`" style="width: 14px" />
                                    </div>
                                </div>
                                <InputText v-model="form.name_known_others" class="w-100" />
                            </div>
                        </div>
                    </div>
                </div>

                <div class="rounded p-5 bg-light border mb-5">
                    <h5 class="card-title text-muted">SEO Bilgileri</h5>
                    <Divider/>
                    <div class="row align-items-center">
                        <label class="col-md-3 fw-bold text-uppercase">SEO Başlık</label>
                        <div class="col-md-9">
                            <InputText class="w-100" v-model="form.seo.title" :class="{'p-invalid':form.errors['seo.title']}"/>
                        </div>
                    </div>
                    <div class="row align-items-center mt-5">
                        <label class="col-md-3 fw-bold text-uppercase">SEO Açıklama</label>
                        <div class="col-md-9">
                            <Textarea autoResize rows="1" cols="30" class="w-100" v-model="form.seo.description" :class="{'p-invalid':form.errors['seo.description']}" />
                        </div>
                    </div>
                </div>

                <div class="rounded p-5 bg-light border mb-5">
                    <h5 class="card-title text-muted">Kısa Açıklama</h5>
                    <Divider/>
                    <TextEditor v-model="form.summary" />
                </div>

                <FormErrors :form="form"/>

                <Divider />

                <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Dropdown from "primevue/dropdown";
import Divider from "primevue/divider";
import PopupMediaLibrary from "../MediaLibrary/PopupMediaLibrary";
import TextEditor from "../../Components/TextEditor";
import Textarea from "primevue/textarea";
import AutoComplete from 'primevue/autocomplete';

const props = defineProps({
    photoCategories:Object,
    photoSubCategory:{
        type:Object,
        default:null
    },
    knownNames:{
        type:Array,
        default:[]
    },
    language:{
        type:String,
        default:'tr'
    },
    fromAttribute:{
        type:Object,
        default:null
    },
    buttonClass:{
        type:String,
        default:'btn fw-bold btn-sm btn-success d-flex align-items-center'
    },
    buttonText:{
        type:String,
        default:'<i class="bi bi-person-plus fs-4"></i><span class="ms-1 lh-1">YENİ</span>'
    },
});

const dialogVisible = ref(false);

const form = useForm({
    photo_sub_category_id:props.photoSubCategory?.id ?? null,
    name:props.fromAttribute ? props.fromAttribute.name+' - '+props.language : "",
    language:props.language,
    name_known:props.fromAttribute ? props.fromAttribute.name_known : "",
    name_known_others:props.fromAttribute ? props.fromAttribute.name_known_others : "",
    uuid:props.fromAttribute ? props.fromAttribute.uuid : "",
    mainImage:props.fromAttribute?.main_image ?? null,
    summary:props.fromAttribute?.summary ?? "",
    seo:{
        title:props.fromAttribute?.seo_data?.title ?? "",
        description:props.fromAttribute?.seo_data?.description ?? ""
    },
});

const setMainImage = (media) => {
    form.mainImage = media;
}

const autoCompleteItems = ref([]);
const search = (event) => {
    console.loh
    autoCompleteItems.value = props.knownNames.filter((x) => x.toLowerCase().startsWith(event.query.toLowerCase()));
}

const create = () => {
    form.post(route('photo-families.store'),{
        onSuccess: () => {
            form.reset();
            dialogVisible.value = false;
        },
    });
}

</script>
